/** @define nav */
.nav {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	padding-left: 0;
	list-style: none;
}
.nav__item {
}
.nav__link {
		padding: 0.5em 1em;
		display: block
}
.nav__link.is-active {
	color: rgba(255, 255, 255, 0.2);
}
.nav__link.is-disabled {
	cursor: not-allowed;
}



